@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Raleway&family=Roboto:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Navbar{background-color:white;width:100%;height:70px;color:black;font-family:'Raleway';font-size:18px;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.Navbar .links{width:500px;height:100%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}.Navbar .name{margin-left:20px;font-family:'Pacifico';font-size:26px;color:#ce8f1b}.Navbar .link:hover{background-color:white;color:black;height:100%;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;cursor:pointer}

.About{display:flex;flex-direction:row;justify-content:center;align-items:center}.About__photo{width:300px;height:300px;display:flex;flex-direction:column;justify-content:center;margin-right:100px}.About__info{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:500px;height:400px;text-align:left;font-family:'Raleway'}img.photo{object-fit:cover;border-radius:50%}

.Header{background-color:#494a46ab;background-image:url("https://www.transparenttextures.com/patterns/food.png");color:white;font-family:'Raleway', serif;height:300px;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;border-top:solid 1px #ce8f1b;border-bottom:solid 1px #ce8f1b}.Header__tweeter{color:black;background-color:rgba(219,214,214,0.904);font-family:'Raleway', serif;width:100%;height:300px;display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;font-size:20px;border-top:solid 1px black;border-bottom:solid 1px black}.Header__demo{width:380px;height:250px;border:solid 1px black}.Header__info{display:flex;flex-direction:column;align-items:flex-start;width:500px;height:300px;text-align:left;font-size:16px}.Header__mapbook{color:black;font-family:'Oranienbaum', serif;height:300px;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}

.Body{display:flex;flex-direction:column}

.Portfolio{display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;width:100%;height:350px;background-color:white}.Portfolio .Portfolio__block{width:280px;height:280px;-webkit-filter:blur(5px) grayscale(70%);filter:blur(5px) grayscale(70%);opacity:0.8;display:flex;flex-direction:row;justify-content:flex-start;transition:0.3s}.Portfolio .Portfolio__block:hover{-webkit-filter:none;filter:none;opacity:1;cursor:pointer}

.App{display:flex;flex-direction:column;text-align:center;font-size:16px}

